import { ref } from 'vue';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
export const useProps = () => {
    return {
        index: {
            type: Number
        },
        varName: {
            type: String,
            default: ''
        },
        varValue: {
            type: String,
            default: ''
        }
    };
};
export const currentRoute = () => {
    const route = useRoute();
    return route;
};
export const useConsoleComponent = (componentProps) => {
    const store = useStore();
    const model = ref(componentProps.varValue);
    const handler = async (value, id) => {
        const { index, varName } = componentProps;
        const payload = {
            index,
            varName,
            varValue: value
        };
        await store.dispatch('setComponentConsoleStyle', payload);
        await store.dispatch('getComponentConsoleStyle', id);
    };
    return {
        model,
        handler
    };
};
